*{
  background-color: rgb(0, 0, 0);
}
.square{
  background: #ffffff;
  
  float: left;
  margin-right: -1px; /*to remove the margin which comes from border*/
  margin-top: -1px;
  padding: 0;
  width: 80px;
  height: 80px;
  text-align: center;
  font-size: 50px;
  font-weight: bold;
  line-height: 80px;
  margin: 5px;
  border-radius: 5px;
}
.square:focus{
  outline: none;
}
/*Board*/
.border-row:after{
  clear: both;
  content: "";
  display: table;
}
/*Game*/
.game{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: row;
  margin: 0 auto;
  
}
/*Game-info*/
li{
  list-style-type:none;
  margin-left: 30px;
}
.stat{
  margin-left: 20px;
  margin-bottom: 20px;
  color: #ee0a0a;
  font-size: 20px;
  
}
.move{
  margin-left: 20px;
  color: #ffffff;
}
.status{
  margin-bottom: 10px;
  font-weight: bold;
}
.right{
    color: #000000;
    margin-bottom: 2px;
    margin-left: 20px;
    border-radius: 5px;
    background-color: aqua;
    padding: 2px;
    
  }
  